<template>
  <div class="create-post">
    <BlogCoverPreview v-show="blogPhotoPreview" />
    <Loading v-show="loading" />
    <div class="container">
      <div :class="{ invisible: !error }" class="err-message">
        <p><span>Error:</span>{{ errorMsg }}</p>
      </div>
      <div class="blog-info">
        <input type="text" placeholder="Enter Blog Title" v-model="blogTitle" />
        <div class="upload-file">
          <label for="blog-photo">Upload Cover Photo</label>
          <input
            type="file"
            ref="blogPhoto"
            id="blog-photo"
            @change="fileChange"
            accept=".png, .jpg, .jpeg"
          />
          <button
            @click="openPreview"
            class="preview"
            :class="{ 'button-inactive': !blogPhotoFileURL }"
          >
            Preview Photo
          </button>
          <span>File Chosen: {{ blogCoverPhotoName }}</span>
        </div>
      </div>
      <div class="editor">
        <QuillEditor
          theme="snow"
          v-model:content="blogHTML"
          contentType="html"
          toolbar="full"
          @image-added="imageHandler"
        />
      </div>
      <div class="blog-actions">
        <button @click="updateBlog">Save Changes</button>
        <router-link class="router-button" :to="{ name: 'BlogPreview' }"
          >Preview Changes</router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router";
import Loading from "../components/Loading.vue";
import { QuillEditor } from "@vueup/vue-quill";
import "@vueup/vue-quill/dist/vue-quill.snow.css";
import BlogCoverPreview from "../components/BlogCoverPreview.vue";
import useEditBlog from "../requests/useEditBlog";

export default {
  name: "CreatePost",
  components: { Loading, BlogCoverPreview, QuillEditor },

  setup() {
    const store = useStore();
    const router = useRouter();
    const route = useRoute();

    const file = ref(null);
    const blogPhoto = ref(null);
    const loading = ref(false);
    const error = ref(null);
    const routeID = ref(null);
    const currentBlog = ref(null);
    const errorMsg = ref(null);
    const editorSettings = ref({
      modules: {
        imageResize: {},
      },
    });

    onMounted(async () => {
      routeID.value = route.params.blogid;
      currentBlog.value = await store.state.blogPosts.filter((post) => {
        return post.blogID === routeID.value;
      });
      store.commit("setBlogState", currentBlog.value[0]);
    });

    const profileId = computed(() => {
      return store.state.profileId;
    });
    const blogPhotoPreview = computed(() => {
      return store.state.blogPhotoPreview;
    });
    const blogPhotoFileURL = computed(() => {
      return store.state.blogPhotoFileURL;
    });
    const blogCoverPhotoName = computed(() => {
      return store.state.blogPhotoName;
    });
    const blogTitle = computed({
      get() {
        return store.state.blogTitle;
      },
      set(payload) {
        store.commit("updateBlogTitle", payload);
      },
    });
    const blogHTML = computed({
      get() {
        return store.state.blogHTML;
      },
      set(payload) {
        store.commit("newBlogPost", payload);
      },
    });

    const fileChange = () => {
      file.value = blogPhoto.value.files[0];
      const fileName = file.value.name;
      store.commit("fileNameChange", fileName);
      store.commit("createFileURL", URL.createObjectURL(file.value));
    };
    const openPreview = () => {
      store.commit("openPhotoPreview");
    };

    const { imageHandler } = useEditBlog();

    const { updateBlog } = useEditBlog(
      blogTitle,
      blogHTML,
      file,
      loading,
      blogCoverPhotoName,
      error,
      errorMsg,
      routeID,
      router,
      store
    );
    // const updateBlog = async () => {
    //   const dataBase = await db.collection("blogPosts").doc(routeID.value);
    //   if (blogTitle.value.length !== 0 && blogHTML.value.length !== 0) {
    //     if (file.value) {
    //       loading.value = true;
    //       const storageRef = storage.ref();
    //       const docRef = storageRef.child(
    //         `documents/BlogCoverPhotos/${blogCoverPhotoName.value}`
    //       );
    //       docRef.put(file.value).on(
    //         "state_changed",
    //         (snapshot) => {
    //           console.log(snapshot);
    //         },
    //         (err) => {
    //           console.log(err);
    //           loading.value = false;
    //         },
    //         async () => {
    //           const downloadURL = await docRef.getDownloadURL();
    //           await dataBase.update({
    //             blogHTML: blogHTML.value,
    //             blogCoverPhoto: downloadURL,
    //             blogCoverPhotoName: blogCoverPhotoName.value,
    //             blogTitle: blogTitle.value,
    //           });
    //           await store.dispatch("updatePost", routeID.value);
    //           loading.value = false;
    //           router.push({
    //             name: "ViewBlog",
    //             params: { blogid: dataBase.id },
    //           });
    //         }
    //       );
    //       return;
    //     }
    //     loading.value = true;
    //     await dataBase.update({
    //       blogHTML: blogHTML.value,
    //       blogTitle: blogTitle.value,
    //     });
    //     await store.dispatch("updatePost", routeID.value);
    //     loading.value = false;
    //     router.push({ name: "ViewBlog", params: { blogid: dataBase.id } });
    //     return;
    //   }
    //   error.value = true;
    //   errorMsg.value = "Please ensure Blog Title & Blog Post has been filled!";
    //   setTimeout(() => {
    //     error.value = false;
    //   }, 5000);
    // };
    return {
      loading,
      error,
      blogPhoto,
      editorSettings,
      updateBlog,
      errorMsg,
      profileId,
      blogCoverPhotoName,
      blogPhotoPreview,
      blogPhotoFileURL,
      blogTitle,
      blogHTML,
      fileChange,
      imageHandler,
      openPreview,
    };
  },
};
</script>

<style>
.create-post {
  position: relative;
  height: 100%;
}
.create-post button {
  margin-top: 0;
}
.router-button {
  text-decoration: none;
  color: #fff;
}
label,
button,
.router-button {
  transition: 0.5s ease-in-out all;
  align-self: center;
  font-size: 14px;
  cursor: pointer;
  border-radius: 20px;
  padding: 12px 24px;
  color: #fff;
  background-color: #303030;
  text-decoration: none;
}
.router-button:hover {
  background-color: rgba(48, 48, 48, 0.7);
}
.container {
  position: relative;
  height: 100%;
  padding: 10px 25px 60px;
}

.invisible {
  opacity: 0 !important;
}
.err-message {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  color: #fff;
  margin-bottom: 10px;
  background-color: #303030;
  opacity: 1;
  transition: 0.5s ease all;
}
.err-message p {
  font-size: 14px;
}
.err-message span {
  font-weight: 600;
}
.blog-info {
  display: flex;
  margin-bottom: 32px;
}
.blog-info input:nth-child(1) {
  min-width: 300px;
}
.blog-info input {
  transition: 0.5s ease-in-out all;
  padding: 10px 4px;
  border: none;
  border-bottom: 1px solid #303030;
}
.blog-info input:focus {
  outline: none;
  box-shadow: 0 1px 0 0 #303030;
}
.upload-file {
  flex: 1;
  margin-left: 16px;
  position: relative;
  display: flex;
}
.upload-file input {
  display: none;
}
.preview {
  margin-left: 16px;
  text-transform: initial;
}
.blog-info span {
  font-size: 12px;
  margin-left: 16px;
  align-self: center;
}

.editor {
  height: 60vh;
  display: flex;
  flex-direction: column;
}
.quillWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.ql-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: scroll;
}
.ql-editor {
  padding: 20px 16px 30px;
}

.blog-actions {
  margin-top: 32px;
}
.blog-actions button {
  margin-right: 16px;
}
</style>
